	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Inside most of us there’s a befunge programmer who wants to come out. When doing day-to-day “serious” programming it is usually a good idea to keep them as firmly locked up as possible. Let’s ignore that instinct for a little while.\n\nIn this talk I’ll try to convince you why you should try writing a completely impractical interpreter of your own. I’ll also use terms like “lexer”, “tokenizer” and “parser” at their widest possible definitions.\n\nTranscript/slides at: http://2014.jsconf.eu/speakers/arne-martin-aurlien-implement-an-esoteric-programming-language-for-fun-and-well-fun.html\n\nLicense: For reuse of this video under a more permissive license please get in touch with us. The speakers retain the copyright for their performances.","meta":{"duration":"PT22M17S"},"presenter":{"name":null},"source":{"id":"oCPT3L33848","provider":"youtube"},"slug":"arne-martin-aurlien-implement-an-esoteric-programming-language-for-fun-or-jsconf-eu-2014","tag":["Arne Martin Aurlien","Esoteric Programming Language","JS","JSConf","JSConf EU","JavaScript"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/oCPT3L33848/default.jpg","height":90,"width":120}},"title":"Arne Martin Aurlien: Implement an Esoteric Programming Language for Fun | JSConf EU 2014"}},{"node":{"description":"Slides: http://www.slideshare.net/floydophone/react-preso-v2\nReact, the new open-source JS library from Facebook and Instagram, is a different way to write JavaScript apps. When it was introduced at JSConf US in May, the audience was shocked by some of its design principles. One sarcastic tweet from an audience member ended up describing React's philosophy quite accurately: https://twitter.com/cowboy/status/339858717451362304\n\nAt Facebook and Instagram, we're trying to push the limits of what's possible on the web with React. My talk will start with a brief introduction to the framework, and then dive into three controversial topics: Throwing out the notion of templates and building views with JavaScript, \"re-rendering\" your entire application when your data changes, and a lightweight implementation of the DOM and events.\n\n\nLicense: For reuse of this video under a more permissive license please get in touch with us. The speakers retain the copyright for their performances.","meta":{"duration":"PT29M32S"},"presenter":{"name":null},"source":{"id":"x7cQ3mrcKaY","provider":"youtube"},"slug":"pete-hunt-react-rethinking-best-practices-jsconf-eu-2013","tag":["JS","JSConf","JSConf EU","JavaScript","JSConf EU 2013","Hunt","React","Facebook (Website)","framework"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/x7cQ3mrcKaY/default.jpg","height":90,"width":120}},"title":"Pete Hunt: React: Rethinking best practices -- JSConf EU 2013"}}]}},"pathContext":{"slug":"jsconf-eu","tag":"JSConf EU"}}

